@import 'bootstrap-normalize.css';
header {
  display: flex;
  justify-content: space-between;
  background-color: rgb(223, 217, 201);
  align-items: center;
  height: 50px;
  position: absolute;
  width: 100%;
  top: 0;
  z-index: 10;
  padding: 0 20px;
}
header .back_button {
  font-size: 34px;
  height: 36px;
  display: flex;
  visibility: hidden;
  justify-content: center;
  align-items: center;
  padding: 0 10px 6px 10px;
  margin-left: -10px;
  cursor: pointer;
  transition: 300ms;
}
header .back_button:hover {
  background-color: rgb(233, 230, 221);
}
header .login_wrapper {
  display: flex;
}
header .login_button {
  font-weight: 600;
  padding: 4px 10px;
  margin-right: -10px;
  cursor: pointer;
  transition: 300ms;
}
header .login_button:hover {
  background-color: rgb(233, 230, 221);
}
header .register_button {
  margin: auto 0;
  padding: 4px 10px;
  cursor: pointer;
  transition: 300ms;
}
header .register_button:hover {
  background-color: rgb(233, 230, 221);
}
header .forms_overlay {
  position: absolute;
  top: 50px;
  left: 0;
  height: calc(100vh - 50px);
  width: 100vw;
  z-index: 9;
  visibility: hidden;
  transition: 300ms;
}
header .login_form {
  position: absolute;
  display: flex;
  flex-direction: column;
  background-color: rgb(223, 217, 201);
  transition: 300ms;
  opacity: 0;
  padding: 10px 12px;
  visibility: hidden;
  top: 60px;
  right: 10px;
  z-index: 10;
  box-shadow: 0 0 40px rgba(0, 0, 0, 0.1);
}
header .login_form input {
  margin-bottom: 5px;
  padding-left: 8px;
  border: none;
  background-color: rgb(240, 240, 240);
}
header .login_form button {
  margin-top: 5px;
  height: 28px;
  background-color: rgb(233, 230, 221);
  transition: 300ms;
}
header .login_form button:hover {
  background-color: rgb(240, 240, 240);
}
header .login_form > div {
  font-size: 14px;
  cursor: pointer;
}
header .login_form > div:hover {
  text-decoration: underline;
}
header .register_form {
  position: absolute;
  display: flex;
  flex-direction: column;
  background-color: rgb(223, 217, 201);
  transition: 300ms;
  opacity: 0;
  width: 300px;
  padding: 10px 12px;
  visibility: hidden;
  top: 60px;
  right: 10px;
  z-index: 10;
  box-shadow: 0 0 40px rgba(0, 0, 0, 0.1);
}
header .register_form input {
  margin-bottom: 5px;
  padding-left: 8px;
  border: none;
  background-color: rgb(240, 240, 240);
}
header .register_form button {
  margin-top: 5px;
  height: 28px;
  background-color: rgb(233, 230, 221);
  transition: 300ms;
}
header .register_form button:hover {
  background-color: rgb(240, 240, 240);
}
header .pass_recovery_form {
  position: absolute;
  display: flex;
  flex-direction: column;
  background-color: rgb(223, 217, 201);
  max-width: 260px;
  transition: 300ms;
  opacity: 0;
  width: 300px;
  padding: 10px 12px;
  visibility: hidden;
  top: 60px;
  right: 10px;
  z-index: 10;
  box-shadow: 0 0 40px rgba(0, 0, 0, 0.1);
}
header .pass_recovery_form label {
  margin-bottom: 5px;
}
header .pass_recovery_form input {
  margin-bottom: 5px;
  padding-left: 8px;
  border: none;
  background-color: rgb(240, 240, 240);
}
header .pass_recovery_form button {
  margin-top: 5px;
  height: 28px;
  background-color: rgb(233, 230, 221);
  transition: 300ms;
}
header .pass_recovery_form button:hover {
  background-color: rgb(240, 240, 240);
}
header .login_info {
  display: none;
}
header .login_info:hover .player_stats {
  visibility: visible;
  opacity: 1;
}
header .nickname {
  cursor: pointer;
  padding: 4px 10px;
}
header .player_stats {
  position: absolute;
  display: flex;
  flex-direction: column;
  background-color: rgb(223, 217, 201);
  transition: 300ms;
  visibility: hidden;
  opacity: 0;
  padding: 10px 12px 4px 12px;
  font-weight: 600;
  width: 200px;
  top: 60px;
  right: 40px;
  z-index: 10;
  box-shadow: 0 0 40px rgba(0, 0, 0, 0.1);
}
header .player_stats .player_stats_item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}
header .logout {
  padding: 4px 10px;
  margin-right: -10px;
  cursor: pointer;
  transition: 300ms;
}
header .logout:hover {
  background-color: rgb(233, 230, 221);
}

.form_active {
  visibility: visible !important;
  opacity: 1 !important;
}

body {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  min-height: 100vh;
  padding: 20px 0;
  background-color: rgb(233, 230, 221);
}

#alert {
  position: absolute;
  top: -40px;
  background-color: rgb(204, 223, 201);
  padding: 5px 10px;
  transition: 400ms top;
  text-align: center;
  max-width: 250px;
  z-index: 999;
}

.tictac_wrapper {
  position: relative;
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100%;
  max-width: 302px;
}
.tictac_wrapper .tictac_title {
  margin-bottom: 20px;
  text-align: center;
}
.tictac_wrapper .game_info {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  width: 100%;
}
.tictac_wrapper .now_turn span {
  font-size: 20px;
  font-weight: bold;
  color: blue;
}
.tictac_wrapper #now_turn_tip {
  font-size: 14px;
  color: rgba(0, 0, 0, 0.5);
  font-weight: normal;
}
.tictac_wrapper .tictac_field {
  display: flex;
  flex-wrap: wrap;
  width: 302px;
  height: 302px;
  background-color: rgb(223, 217, 201);
  border: 1px solid black;
}
.tictac_wrapper .tictac_cell {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid black;
  width: 100px;
  height: 100px;
  cursor: pointer;
}
.tictac_wrapper .result_popup {
  position: absolute;
  text-align: center;
  left: -100%;
  width: 270px;
  top: 60%;
  opacity: 0;
  transform: translate(-50%, -50%);
  transition: 500ms;
  backdrop-filter: blur(6px);
  padding: 25px 35px;
}
.tictac_wrapper .score {
  font-size: 20px;
  margin-top: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.tictac_wrapper .score span {
  font-size: 24px;
  margin: 0 8px;
  font-weight: 600;
}
.tictac_wrapper .score span:nth-child(1) {
  color: blue;
}
.tictac_wrapper .score span:nth-child(2) {
  color: red;
}
.tictac_wrapper .win_message {
  margin-top: -5px;
}
.tictac_wrapper #newGame {
  width: 150px;
  height: 34px;
  background-color: rgba(255, 255, 255, 0.6);
  margin-top: 18px;
}

.tictac_subtitle {
  align-self: start;
}

.create_room_popup {
  box-shadow: 0 0 40px rgba(0, 0, 0, 0.1);
  background-color: rgb(223, 217, 201);
  transform: translate(-50%, -50%);
  justify-content: center;
  padding: 10px 12px;
  position: absolute;
  transition: 400ms;
  flex-wrap: wrap;
  display: flex;
  left: -100%;
  opacity: 0;
  top: 50%;
}
.create_room_popup label {
  margin-bottom: 5px;
  font-size: 18px;
}
.create_room_popup button {
  margin-top: 6px;
  padding: 0 19px;
  height: 28px;
  background-color: rgb(233, 230, 221);
  transition: 300ms;
}
.create_room_popup button:hover {
  background-color: rgb(240, 240, 240);
}
.create_room_popup button[type=submit] {
  margin-right: auto;
}
.create_room_popup input {
  margin-bottom: 5px;
  padding-left: 8px;
  width: 200px;
  border: none;
  background-color: rgb(240, 240, 240);
}

.game_mods {
  width: 100%;
}

.game_mod {
  display: flex;
  align-items: center;
  margin-bottom: 12px;
  background-color: rgb(223, 217, 201);
  padding: 10px 20px;
  cursor: pointer;
  transition: 300ms background;
}
.game_mod:hover {
  background-color: rgb(235, 226, 207);
}
.game_mod .mod_icon {
  height: 20px;
}
.game_mod .icon1 {
  margin-left: 6.88px;
  margin-right: 26.88px;
}
.game_mod .icon2 {
  margin-left: 5.63px;
  margin-right: 25.63px;
}
.game_mod .icon3 {
  margin-right: 20px;
}

.online_mod {
  background-color: rgb(223, 217, 201);
  margin-top: -13px;
  width: 100%;
  max-height: 0px;
  overflow: scroll;
  border-bottom: 1px solid rgb(223, 217, 201);
  transition: 600ms max-height;
}
.online_mod h6 {
  margin: 4px 0 10px 18px;
}
.online_mod .rooms {
  text-align: center;
}
.online_mod .room {
  display: flex;
  justify-content: space-between;
  background-color: rgb(233, 230, 221);
  padding: 4px 18px;
  margin: 8px 0;
  cursor: pointer;
  transition: 300ms background;
}
.online_mod .room:hover {
  background-color: rgb(240, 240, 240);
}
.online_mod button {
  display: flex;
  margin: 6px auto 8px auto;
  background-color: rgb(233, 230, 221);
  padding: 2px 30px;
  transition: 300ms background;
  justify-content: center;
}
.online_mod button:hover {
  background-color: rgb(240, 240, 240);
}
.online_mod::-webkit-scrollbar {
  width: 0;
  height: 0;
}